import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    lang: window.localStorage.getItem('lang') || 'zh-CN',
    userInfoObj:JSON.parse(window.localStorage.getItem('userInfoObj')) || {}
  },
  mutations: {
    changeLang(state, lang) {
      state.lang = lang;
      window.localStorage.setItem('lang', lang);
    },
    userInfo(state,userInfoObj){
      state.userInfoObj = userInfoObj
      window.localStorage.setItem('userInfoObj',JSON.stringify(userInfoObj))
    }
  },
  actions: {
  },
  modules: {
  }
})
